import _findIndex from 'lodash/findIndex';

import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { Customer } from '@package/shared-consts';
import { CUSTOMER_DEFAULT_VALUES } from 'features/customer/create/form/consts';

// ----------------------------------------------------------------------
export type CreateCustomerState = {
  formData: Customer.Info[];
  customer: Customer.Info;
};
const initialState: CreateCustomerState = {
  formData: [],
  customer: CUSTOMER_DEFAULT_VALUES.DEFAULT
};

export const createCustomerSlice = createSlice({
  name: 'customer',
  initialState,
  reducers: {
    saveCustomerData: (state, action: PayloadAction<{ data: Customer.Info }>) => {
      const createData = action.payload.data;
      state.customer = { ...createData };

      const index = _findIndex(state.formData, (data: Customer.Info) => {
        return data.id === createData.id;
      });
      if (index >= 0) state.formData[index] = createData;
      else {
        state.formData = [...state.formData, createData];
      }
    }
  }
});

export const { saveCustomerData } = createCustomerSlice.actions;

export default createCustomerSlice.reducer;
