export var Terms;
(function (Terms) {
    Terms.Enum = {
        PERSONAL_INFORMATION: {
            name: 'PERSONAL_INFORMATION',
            desc: '개인(신용)정보 수집 및 이용약관',
        },
        USED_COLLECTION: {
            name: 'USED_COLLECTION',
            desc: '개인정보 수집이용동의',
        },
    };
    Terms.values = () => {
        return Object.values(Terms.Enum);
    };
    Terms.findBy = (name) => {
        return Terms.Enum[name] ?? {};
    };
    Terms.of = (key) => {
        return Terms.Enum[key] ? Terms.Enum[key].name : '';
    };
})(Terms || (Terms = {}));
