import { API_GATEWAY_URL } from 'config/host';
import { httpClient } from 'utils';

import { Customer, ExternalLinkType, User } from '@package/shared-consts';
import { QueryKey, UseQueryOptions, useQuery } from '@tanstack/react-query';
import { AxiosError } from 'axios';

// ----------------------------------------------------------------------

const URLS = {
  // GET
  USER_PROFILE: `${API_GATEWAY_URL}/auth/me`,
  EXTERNAL: `${API_GATEWAY_URL}/main/external/link`,
  // POST
  LOGIN: `${API_GATEWAY_URL}/auth/login`
};

// FETCHERS

export async function signInApi(username: string, password: string) {
  const { data } = await httpClient.post<{ accessToken: string }>(URLS.LOGIN, {
    username,
    password
  });
  return data?.accessToken;
}

export async function getUserProfileApi() {
  const { data } = await httpClient.get<User.Profile>(URLS.USER_PROFILE);
  return data;
}

export async function checkCertifyLink(key: string, type: ExternalLinkType.Keys) {
  const { data } = await httpClient.get<Customer.CertifyLink.Check>(
    URLS.EXTERNAL + `/${type}/${key}/check`
  );
  return data;
}

// RQ HOOKS
export function useGetCheckCertifyLink<TData = Customer.CertifyLink.Check>(
  key: string,
  type: ExternalLinkType.Keys,
  options?: UseQueryOptions<Customer.CertifyLink.Check, AxiosError, TData>
) {
  return useQuery({
    queryKey: [`check-certify-link_${type}_${key}`] as QueryKey,
    queryFn: () => checkCertifyLink(key, type),
    ...options
  });
}

