import { useEffect, memo, useMemo, useCallback } from 'react';
import _map from 'lodash/map';
import _size from 'lodash/size';

import { useAuthContext } from 'hooks/useAuthContext';
import { useDispatch, useSelector } from 'store';
import { setActiveSubmenu } from 'store/slices/gnb';
import { useNavigate, useLocation } from 'react-router-dom';
import { PATH_AFTER_LOGIN, PATH_DASHBOARD } from 'routes/paths';

import Logo from 'components/ui/Logo';
import Typography from 'components/ui/Typography';
import Icon from 'components/ui/Icon';
import Button from 'components/ui/Button';
import { Flex } from 'components/ui';

import classNames from 'classnames';
import './index.scss';

// ----------------------------------------------------------------------

function filterNavByType(nav: Gnb.NavItem[], type: Gnb.NavType) {
  return nav.filter(({ itemType }) => itemType === type);
}

const Header = ({ mode }: { mode: Gnb.NavMode }) => {
  const { logout } = useAuthContext();
  const { activeSubmenu, ...nav } = useSelector(({ gnb }) => gnb);
  const { customer } = useSelector(({ customer }) => customer);
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const { pathname } = useLocation();

  const { navItems, optionItems } = useMemo(() => {
    const _nav = mode === 'advisor' ? nav.advisorNav : nav.clientNav;
    return {
      navItems: filterNavByType(_nav, 'nav'),
      optionItems: filterNavByType(_nav, 'option')
    };
  }, [mode, nav]);

  useEffect(() => {
    const [, , , pathSubmenu] = pathname.split('/');
    // ['','dashboard', 'MODE', 'PATH_SUBMENU']
    dispatch(setActiveSubmenu({ itemKey: pathSubmenu, mode }));
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [pathname]);

  const onHandleClickNav = useCallback((item: Gnb.NavItem) => {
    if (item.children) dispatch(setActiveSubmenu({ itemKey: item.id, mode }));
    if (item.path) navigate(item.path);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const onHandleClickOption = useCallback(
    (item: Gnb.NavItem) => {
      if (item.children) dispatch(setActiveSubmenu({ itemKey: item.id, mode }));
      if (item.path) navigate(item.path);
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    [mode]
  );

  const onHandleClickActiveMenu = useCallback((item: Gnb.NavItemBase) => {
    if (item.path) navigate(item.path);
    if (item.actionType) {
      switch (item.actionType) {
        case 'logout':
          logout(true);
          break;
        case 'dashboard':
          navigate(PATH_AFTER_LOGIN);
          break;
        default:
          return;
      }
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <div className="qb-header">
      <div className="flex items-center justify-between relative z-[99] w-full h-16 px-[1.875rem] bg-white rounded-b-md shadow-[.25rem_.25rem_.625rem_0_rgba(0,0,0,0.10)]">
        <div className="qb-header__title-area">
          <button
            type="button"
            onClick={() => {
              if (mode === 'advisor') {
                navigate(PATH_AFTER_LOGIN);
              } else navigate(PATH_DASHBOARD.client.overview);
            }}
            className="qb-header__title-touchable"
          >
            {mode === 'client' ? (
              <Flex>
                <Typography weight={700} color="text-primary-600">
                  {customer.name}
                </Typography>
                <Typography size="sm" color="text-gray-700">
                  님의
                </Typography>
                <div className="inline-flex items-center justify-center w-[2.625rem] h-[2.625rem] ml-0.5 bg-gray-200 rounded-full">
                  <Typography variant="caption" weight={700} color="text-primary-700">
                    PLAN
                  </Typography>
                </div>
              </Flex>
            ) : (
              <Logo corporate={'Better-Wealth'} />
            )}
          </button>
        </div>
        {/*  NAV  */}
        <ul className="qb-header__nav">
          {_map(navItems, (item) => (
            <li
              key={item.id}
              className={classNames('qb-header__nav-item', {
                'qb-header__nav-item--active': activeSubmenu?.id === item.id
              })}
            >
              <Button
                variant="ghost"
                onClick={() => onHandleClickNav(item)}
                className="qb-header__nav-item-pressable"
              >
                <Typography weight={700} className="qb-header__nav-item-label">
                  {item.title}
                </Typography>
              </Button>
            </li>
          ))}
        </ul>
        {/* OPTIONS */}
        <ul className="qb-header__options">
          {_map(optionItems, (item) => (
            <li key={item.id} className="qb-header__option">
              <Icon
                name={item.icon ?? 'icon-basic-setting'}
                onClick={() => onHandleClickOption(item)}
                className={classNames('qb-header__option-icon', {
                  'qb-header__option-icon--active': activeSubmenu?.id === item.id
                })}
              />
            </li>
          ))}
        </ul>
      </div>
      {/* ACTIVE SUBMENU */}
      <div
        className={classNames('qb-sub-nav', `qb-sub-nav--${activeSubmenu.itemType}`, {
          'qb-sub-nav--active': _size(activeSubmenu?.children) > 0
        })}
      >
        <ul className="qb-sub-nav__list">
          {_map(activeSubmenu?.children, (item) => (
            <li
              key={item.id}
              className={classNames('qb-sub-nav__item', {
                'qb-sub-nav__item--active': item.path ? item.path?.includes(pathname) : false
              })}
              onClick={() => onHandleClickActiveMenu(item)}
            >
              <button type="button" className="qb-sub-nav__item-pressable">
                <Typography size="sm" className="qb-sub-nav__item-label">
                  {item.title}
                </Typography>
                <i className="qb-sub-nav__item-underline" />
              </button>
            </li>
          ))}
        </ul>
      </div>
    </div>
  );
};

const MemoizedHeader = memo(Header);
export default MemoizedHeader;
