import _map from 'lodash/map';
import _get from 'lodash/get';
import _keys from 'lodash/keys';
import dayjs from 'dayjs';
import { OptionProps } from 'components/forms/Select';
import { Assets, Pagination } from '@package/shared-consts';

// ----------------------------------------------------------------------

export function makeOptionList(values: { name: string | number; desc: string | number }[]) {
  const list: OptionProps[] = [];
  _map(values, (value, index) => {
    list.push({
      id: index,
      label: value.desc.toString(),
      value: value.name
    });
  });
  return list;
}
export function makeAssetsTypeList(key: Assets.Keys) {
  const optionList = Assets.Enum;
  const items = _get(optionList, key).items;
  const list: OptionProps[] = [];
  _map(items, (item) => {
    const itemKey = _keys(item)[0];
    list.push({
      id: _get(item, itemKey).id,
      label: _get(item, itemKey).label,
      value: _get(item, itemKey).value
    });
  });
  return list;
}
export function makeYearList(until: number) {
  const now = dayjs().year();

  const list: OptionProps[] = [];
  _map([...Array(until).keys()], (key, index) => {
    list.push({
      id: index,
      label: (now + key).toString() + '년',
      value: (now + key).toString()
    });
  });
  return list;
}
export function getTableNo(
  meta: Pagination.Metadata | undefined,
  index: number,
  value: string,
  query: Partial<Pagination.QueryParams>
) {
  if (!meta) return value;
  const orderBy = query.orderBy?.includes('asc') ? 'asc' : 'desc';
  const total = meta.total;
  const currentPage = meta.currentPage;
  const perPage = meta.perPage;
  switch (orderBy) {
    case 'asc':
      return index + 1 + (currentPage - 1) * perPage;
    case 'desc':
      return total - (currentPage - 1) * perPage - index;
  }
}
