import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { ConfirmProps } from 'components/ui/Confirm';

// ----------------------------------------------------------------------
export type DialogState = { options: ConfirmProps };

// const initialState: DialogState = { isOpen: false };
const initialState: DialogState = {
  options: {
    isOpen: false,
    children: undefined
  }
};
export const dialogSlice = createSlice({
  name: 'dialog',
  initialState,
  reducers: {
    openDialog: (state, actions: PayloadAction<ConfirmProps>) => {
      state.options = { ...actions.payload };
      state.options.isOpen = true;
    },
    closeDialog: (state) => {
      state.options.isOpen = false;
    }
  }
});

export const { openDialog, closeDialog } = dialogSlice.actions;

export default dialogSlice.reducer;
