import axios, { AxiosError } from 'axios';
import { StatusCodes } from 'config/http';

import { setSession } from './jwtUtils';

// ----------------------------------------------------------------------

const httpClient = axios.create();

// TODO: add interceptors: request | response handlers

httpClient.interceptors.response.use(
  (response) => response,
  (error: AxiosError) => {
    console.log('axios response ', error.response);
    if (error.response && error.response.status) {
      switch (error.response.status as StatusCodes) {
        // case StatusCodes.PRECONDITION_FAILED:
        // case StatusCodes.FORBIDDEN:
        // case StatusCodes.INTERNAL_SERVER_ERROR:
        case StatusCodes.UNAUTHORIZED:
          setSession(null);
          break;
        default:
          return Promise.reject(error);
      }
    }

    return Promise.reject(error);
  }
);

export const ReportErrorAlert = (error: unknown) => {
  if (error instanceof AxiosError) {
    const { response } = error;
    if (response?.data.message) alert(response?.data.message);
    else alert('오류가 발생하였습니다.');
  }
};

export default httpClient;
